main {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .avatar {
        border-radius: 50%;
        border: 1px solid #000000;
    }

    h1 {
        margin: 20px;
        font-size: 3rem;
        text-align: center;
    }

    p {
        font-size: 1.5rem;
        margin: 10px;
        text-align: center;
    }
}
