* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    height: 100%;
    font-family: 'Ubuntu', sans-serif;
    font-size: 16px;
    color: #000000;
    text-rendering: optimizeLegibility !important;
    -webkit-font-smoothing: antialiased !important;
    background-color: lightgrey;
}

#root {
    height: 100%;
    max-width: 1040px;
    margin: auto;
    display: flex;
    flex-direction: column;
}

img {
    object-fit: cover;
    max-width: 100%;
    display: block;
}
