footer {
    margin: 50px 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    ul {
        width: 180px;
        margin: 10px 0;
        list-style-type: none;
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;

        li {
            display: flex;
            flex-direction: column;
            align-items: center;

            a {
                color: inherit;
                text-decoration: none;
            }

            a:hover {
                color: inherit;
            }
        }
    }
}
